/* RESET */
* {
  margin: 0;
  padding: 0;

  box-sizing: border-box;

  list-style: none;

  text-decoration: none;
}

/* Font */
@import url('https://fonts.googleapis.com/css2?family=Hind:wght@300;400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700;900&display=swap');

body::-webkit-scrollbar {
  background-color: black;

  width: 12px;
}

body::-webkit-scrollbar-button {
  height: 50px;
  scroll-margin-bottom: -150px;
}

body::-webkit-scrollbar-thumb {
  background-color: blue;

  border: .5px solid rgba(128, 0, 128, 0.608);

  border-radius: 5px;

  color: black;

  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.563);
}

body::-webkit-scrollbar-track {
  background-color: transparent;
}

html, body {
  height: 100vh;
  width: 100vw;

    

  background: center center no-repeat fixed;
  background-size: cover;
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.874), rgba(18, 18, 18, 0.792)), url(./images/bg.png);

  overflow-x: hidden;

  font-family: 'Orbitron', sans-serif;
}

p {
  font-family: 'Hind', sans-serif;
  font-weight: 300;

  text-shadow: 1px 1px rgba(0, 0, 0, 0.622);
}

strong {
  font-weight: 700;
}

@media (max-width:693px) {
  body::-webkit-scrollbar-button {
    height: 100px;
  }
}