.Projects_container {
    max-width: 90%;
    height: max-content;

    margin: 0 auto;
    padding: 50px 5%;

    display: flex;
    flex-wrap: wrap;

    color: white;
}

.Projects_container nav {
    grid-row: 3;

    height: min-content;

    display: flex;
    align-items: center;

    margin: 5px;

    column-gap: 10px;
}

.Projects_container nav svg {
    background-color: rgba(0, 0, 0, 0.53);

    box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.512);
}

.Projects_container h2 {
    text-align: center;

    width: 100%;

    margin-bottom: 100px;

    font-size: 3em;
}

.Projects_container>ul {
    width: 50%;

    display: grid;

    row-gap: 150px;
}

.Projects_container h3 {
    height: min-content;

    text-align: center;
}

.Projects_container>ul>li {
    max-height: 400px;
    width: 100%;

    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 600px));
    grid-template-rows: repeat(3, min-content);

    column-gap: 20px;
    row-gap: 5px;
}

.Projects_container>ul>li>ul {
    grid-row: 3;

    display: flex;

    column-gap: 20px;

    margin-top: 10px;

    z-index: 3;
}

.Projects_container svg {
    font-size: 2em;
}

.Projects_container p {
    grid-column: 1;

    text-align: justify;

    font-size: .9em;
}

.Projects_container li {
    z-index: 3;
}

.Projects_container img {
    grid-row: 1/4;
    grid-column: 2;

    height: auto;
    width: 100%;

    position: relative;
    top: 50%;
    transform: translateY(-50%);

    border: 1px solid rgb(44, 44, 44);
    border-radius: 10px;

    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.453);
}

.Projects_container img:hover {
    transition: .6s;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.453);
}

.Projects_container_rigthPosition {
    grid-template-columns: 50% 50%;

}

.Projects_container_leftPosition {
    grid-template-columns: 50% 50%;

}

.Projects_container_leftPosition>li {
    grid-column: 1;
}

.Projects_container_leftPosition nav {
    grid-column: 1;

    justify-content: start;
}

.Projects_container_leftPosition>li>ul {
    grid-column: 1;

    justify-content: end;
}

.Projects_container_rigthPosition>li {
    grid-column: 2;
    width: 100%;

    margin-top: 440px;
    margin-left: -600px;
}

.Projects_container_rigthPosition h3 {
    grid-column: 2;
}

.Projects_container_rigthPosition nav {
    grid-column: 2;

    justify-content: end;
}

.Projects_container_rigthPosition p {
    grid-column: 2;
}

.Projects_container_rigthPosition>li>ul {
    grid-column: 2;

    justify-content: start;
}

.Projects_container_rigthPosition img {
    grid-column: 1;

}

@media (max-width: 893px) {
    .Projects_container {
        display: grid;
        grid-template-columns: 60% 60%;
    }

    .Projects_container h2 {
        grid-row: 1;
        grid-column: 1/3;
        
        position: relative;
        left: 50vw;
        transform: translateX(-55vw);
    }

    .Projects_container p {    
        text-align: center;
    }

    .Projects_container>ul {
        width: 100%;

        display: flex;
        flex-direction: column;

        row-gap: 800px;
    }

    .Projects_container>ul>li {
        display: flex;
        flex-direction: column;

        width: 100%;
        height: min-content;

        margin: 0 auto;
    }

    .Projects_container img {
        position: relative;
        top: auto;
        transform: translateY(0);
    }

    .Projects_container nav {
        position: relative;
        bottom: 50px;
    }

    .Projects_container_rigthPosition {
        grid-column: 2;
        width: 100%;

        margin-top: 625px;
        margin-left: -130px;

    }

    .Projects_container_leftPosition {
        grid-column: 1;
        width: 100%;
    }

    .Projects_container_rigthPosition h3 {
        text-align: right;
    }

    .Projects_container_leftPosition h3 {
        text-align: left;
    }
}

@media (max-width: 693px) {
    .Projects_container {
        grid-template-columns: 80% 80%;
    }

    .Projects_container_rigthPosition {
        margin-top: 625px;
        margin-left: -73%;
    }
}

@media (max-width: 693px) {
    .Projects_container {
        grid-template-columns: 100% 100%;
    }

    .Projects_container h2 {
        transform: translateX(-100vw);
    }

    .Projects_container_rigthPosition {
        margin-top: 625px;
        margin-left: -100%;
    }
}